<template>
  <div class="hello">
    <h1>{{ msg }}</h1>
    <input v-model="newTodo" @keydown.enter="addToList">
    <button @click="addToList">Add</button>
    <button @click="deleteAllList">Delete All</button>
    <br/>
    {{ newTodo}}
    <br/>
    <ul>
      <li v-for="(todo, index) in todos" :key="index">
        <span v-if="!todo.edit">{{ todo.text }}</span>
        <input v-else v-model="todo.text" @keydown.enter="editTodo(todo)">
        <i v-if="!todo.edit" class="material-icons" @click="deleteTodo(index)">delete</i>
        <i v-if="!todo.edit" class="material-icons" @click="editTodo(todo)">edit</i>
        <i v-else class="material-icons" @click="editTodo(todo)">save</i>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'HomePage',
  data () {
    return {
      msg: 'Welcome yappy',
      newTodo:'',
      todos:[
        { text: 'wash clothes',
          edit: false
        }
      ]
    }
  },
  methods:{
    addToList: function(){
      if(this.newTodo.length > 0){
        this.todos.push({
          text: this.newTodo,
          edit: false
        }) 
      }
      else{
        alert('please input');
      }
       
    },
    deleteTodo: function(index){
      this.todos.splice(index,1)
    },
    deleteAllList: function(index){
      this.todos.splice(index)
    },
    editTodo: function(todo) {
      todo.edit = !todo.edit//false

    }

  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  li{
    height: 25px;
  }
  i{
    float: right;
  }
</style>



